.App {
  text-align: center;
  
}
body{
  background-color: hsl(345, 95%, 68%);
}
.body{
  text-align: center;
  position: absolute;
  top: 30%;
  left: 30%;

}
.days,.hour,.minutes,.seconds{
  display: inline-block;
  padding: 20px;

}
.daydiv,.hourdiv,.minutediv,.seconddiv{
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color: hsl(236, 21%, 26%);
  border-radius: 5px;
  color: white;
}
.body h4{
  color: white;
}

.start{
  background-color: hsl(236, 21%, 26%) ;
  width: 100px;
  height: 50px;
  padding: 10px;
  border-radius: 10px;
  color: white;
  margin: 10px;

}
